// MUI
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import { useSnackbar } from 'src/components/snackbar';
// route
import { paths } from 'src/routes/paths';
// hooks
import { useState } from 'react';
import { ConfirmDialog } from 'src/components/custom-dialog';
import { useBoolean } from 'src/hooks/use-boolean';

// ----------------------------------------------------------------------

export default function LearningOnlineView() {
  const { enqueueSnackbar } = useSnackbar();

  const [roomName, setRoomName] = useState('');

  const openDialog = useBoolean();

  const handleJohnRoom = () => {
    if (roomName.length < 16) {
      enqueueSnackbar('Tên phòng phải từ 16 ký tự !', { variant: 'warning' });
    } else {
      openDialog.onFalse();
      window.open(`https://meet.jit.si/${roomName}`, '_blank');
    }
  };

  return (
    <>
      <CustomBreadcrumbs
        heading="Học tập / họp trực tuyến"
        links={[
          { name: 'Tiện ích' },
          {
            name: 'Trực tuyến',
            href: paths.home.learning_online,
          },
          { name: 'Tạo / tham gia' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Stack direction="row" sx={{ mx: 3, mb: 2 }}>
        <Button variant="contained" color="primary" onClick={openDialog.onTrue}>
          Tạo / tham gia kênh trực tuyến
        </Button>
      </Stack>

      <ConfirmDialog
        open={openDialog.value}
        onClose={openDialog.onFalse}
        title="Tạo phòng trực tuyến của bạn"
        content={
          <>
            <TextField
              value={roomName}
              onChange={(event: any) => setRoomName(event.target.value)}
              sx={{ width: '100%' }}
              placeholder="Nhập tên phòng để tạo phòng / tham gia..."
            />
          </>
        }
        action={
          <Button variant="contained" color="primary" disabled={!roomName} onClick={handleJohnRoom}>
            Tạo / tham gia phòng
          </Button>
        }
      />
    </>
  );
}
